import { useNavigate } from "react-router-dom";

import "./noExiste.css";

// --------------------------------------------
// --------------------------------------------
// - Componente para mostrar pgina No existe -
// --------------------------------------------
// --------------------------------------------
const NoExiste = () => {
  const navegar = useNavigate();

  // --------------------------------------------
  // - Mtodo para volver a la pgina de inicio -
  // --------------------------------------------
  const volverPaginaInicio = () => {
    navegar("/");
  };

  // -------
  // - JSX -
  // -------
  return (
    <div className="noExiste__contenedor">
      <h2>Esa pgina no existe</h2>
      <button onClick={volverPaginaInicio}>Volver</button>
    </div>
  );
};

export default NoExiste;
