import axios from "axios";

// const BASE_URL = "http://localhost:5000";
const BASE_URL = "https://aplicacion-panstiago.onrender.com";

// ----------------------------------------------
// - Mtodo utilizado para realizar la peticin -
// - a una ruta pblica del servidor            -
// ----------------------------------------------
export default axios.create({
  baseURL: BASE_URL,
});

// ------------------------------------------------
// - Mtodo utilizado para realizar la peticin   -
// - a una ruta privada del servidor              -
// - El parmetro withCredentials se utiliza      -
// - para enviar el cookie en el que se encuentra -
// - el refresh token                             -
// ------------------------------------------------
export const axiosPrivado = axios.create({
  baseURL: BASE_URL,
  withCredentials: true,
});
